import { Component, OnInit, Optional, Input, Output, EventEmitter, Inject } from '@angular/core';
import { Location } from '@angular/common';
import { Language, LoadingAppToken, LoadingApp, AppRouter, DefaultLoadingApp } from '@allianzSND/core';
import { fromEvent } from 'rxjs';
@Component({
  selector: 'app-loading',
  templateUrl: './loading.component.html',
  styleUrls: ['./loading.component.scss']
})
export class LoadingComponent implements OnInit {

  // progress bar 
  @Input() progressValue = 0.2;
  @Output() onProgressFinish = new EventEmitter();
  public language: Language = new Language();
  private loadingAppHandler: LoadingApp;
  constructor(
    private router: AppRouter,
    defaultLoadingAppHandler: DefaultLoadingApp,
    @Optional() private _location: Location,
    @Optional() @Inject(LoadingAppToken) loadingAppHandler: LoadingApp
  ) {
    this.loadingAppHandler = loadingAppHandler || defaultLoadingAppHandler;
  }

  progressFinish() {
    console.log('in  loading finish percent:', this.progressValue);
    if (this.progressValue >= 1)
      this.onProgressFinish.emit();
  }


  // svg get url;
  getUrl() {
    if (this._location) {
      let urlLink = this._location.path();
      // console.log(urlLink.replace(/\//,''));
      return urlLink.replace(/\//, '');
    }
    else
      return '';
  }

  ngOnInit() {
    fromEvent(document, 'offline').subscribe(() => {
      if (this.loadingAppHandler && this.progressValue < 1) {
        this.router.navigate("Login");
      }
    })
  }


}
